import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { useGame } from '../context/GameContext';

const ModalOverlay = styled(motion.div)`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(10px);
`;

const ModalContent = styled(motion.div)`
  background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
  border: 3px solid #00d4ff;
  border-radius: 20px;
  padding: 30px;
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 0 50px rgba(0, 212, 255, 0.5);
  font-family: 'Roboto', sans-serif;
`;

const QuestionHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 2px solid rgba(0, 212, 255, 0.3);
`;

const QuestionTitle = styled.h2`
  color: #00d4ff;
  font-family: 'Orbitron', monospace;
  font-size: 1.5rem;
  margin: 0;
  text-shadow: 0 0 10px rgba(0, 212, 255, 0.5);
`;

const DifficultyBadge = styled.span`
  background: ${props => {
    switch (props.difficulty) {
      case 'easy': return 'linear-gradient(45deg, #00ff88, #00d4ff)';
      case 'medium': return 'linear-gradient(45deg, #ffa500, #ff6b6b)';
      case 'hard': return 'linear-gradient(45deg, #ff1744, #d50000)';
      default: return 'linear-gradient(45deg, #00d4ff, #00ff88)';
    }
  }};
  color: white;
  padding: 5px 15px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: bold;
  text-transform: uppercase;
  box-shadow: 0 0 15px rgba(0, 212, 255, 0.3);
`;

const QuestionText = styled.div`
  color: white;
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 25px;
  background: rgba(0, 0, 0, 0.3);
  padding: 20px;
  border-radius: 10px;
  border-left: 4px solid #00d4ff;
`;

const OptionsContainer = styled.div`
  margin-bottom: 25px;
`;

const OptionButton = styled(motion.button)`
  width: 100%;
  background: ${props => {
    if (props.selected) {
      return props.correct ? 
        'linear-gradient(45deg, #00ff88, #00d4ff)' : 
        'linear-gradient(45deg, #ff6b6b, #ff8e8e)';
    }
    return 'rgba(255, 255, 255, 0.1)';
  }};
  border: 2px solid ${props => {
    if (props.selected) {
      return props.correct ? '#00ff88' : '#ff6b6b';
    }
    return 'rgba(0, 212, 255, 0.3)';
  }};
  border-radius: 10px;
  padding: 15px 20px;
  margin-bottom: 10px;
  color: white;
  font-size: 1rem;
  text-align: left;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  
  &:hover {
    background: rgba(0, 212, 255, 0.2);
    border-color: #00d4ff;
    transform: translateX(5px);
  }
  
  &:disabled {
    cursor: not-allowed;
    opacity: 0.7;
  }
`;

const OptionLabel = styled.span`
  font-weight: bold;
  margin-right: 10px;
  color: #00d4ff;
`;

const ButtonContainer = styled.div`
  display: flex;
  gap: 15px;
  justify-content: center;
  margin-top: 20px;
`;

const ActionButton = styled(motion.button)`
  background: ${props => {
    switch (props.variant) {
      case 'primary': return 'linear-gradient(45deg, #00d4ff, #00ff88)';
      case 'secondary': return 'linear-gradient(45deg, #ff6b6b, #ff8e8e)';
      case 'hint': return 'linear-gradient(45deg, #ffa500, #ffb84d)';
      default: return 'linear-gradient(45deg, #4a90e2, #6bb6ff)';
    }
  }};
  border: none;
  border-radius: 10px;
  padding: 12px 25px;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 0 20px rgba(0, 212, 255, 0.3);
  transition: all 0.3s ease;
  
  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 25px rgba(0, 212, 255, 0.5);
  }
  
  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    transform: none;
  }
`;

const HintContainer = styled(motion.div)`
  background: rgba(255, 165, 0, 0.1);
  border: 2px solid #ffa500;
  border-radius: 10px;
  padding: 15px;
  margin: 15px 0;
  color: #ffa500;
  font-style: italic;
`;

const FeedbackContainer = styled(motion.div)`
  background: ${props => props.correct ? 
    'rgba(0, 255, 136, 0.1)' : 
    'rgba(255, 107, 107, 0.1)'};
  border: 2px solid ${props => props.correct ? '#00ff88' : '#ff6b6b'};
  border-radius: 10px;
  padding: 15px;
  margin: 15px 0;
  color: ${props => props.correct ? '#00ff88' : '#ff6b6b'};
`;

const CloseButton = styled(motion.button)`
  background: none;
  border: none;
  color: #00d4ff;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 5px;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  
  &:hover {
    background: rgba(0, 212, 255, 0.2);
  }
`;

const QuestionModal = () => {
  const { state, hideQuestionModal, submitAnswer } = useGame();
  const { showQuestionModal, currentQuestion } = state;
  const [selectedAnswer, setSelectedAnswer] = useState(null);
  const [showHint, setShowHint] = useState(false);
  const [showFeedback, setShowFeedback] = useState(false);
  const [isCorrect, setIsCorrect] = useState(false);
  const [answered, setAnswered] = useState(false);

  // Reset state when modal opens/closes
  useEffect(() => {
    if (showQuestionModal) {
      setSelectedAnswer(null);
      setShowHint(false);
      setShowFeedback(false);
      setAnswered(false);
    }
  }, [showQuestionModal]);

  const handleAnswerSelect = (answerIndex) => {
    if (answered) return;
    setSelectedAnswer(answerIndex);
  };

  const handleSubmit = () => {
    if (selectedAnswer === null || answered) return;
    
    const correct = submitAnswer(selectedAnswer);
    setIsCorrect(correct);
    setAnswered(true);
    setShowFeedback(true);
  };

  const handleHint = () => {
    setShowHint(true);
  };

  const handleClose = () => {
    hideQuestionModal();
  };

  const handleNext = () => {
    hideQuestionModal();
  };

  if (!showQuestionModal || !currentQuestion) {
    return null;
  }

  return (
    <AnimatePresence>
      <ModalOverlay
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        onClick={handleClose}
      >
        <ModalContent
          initial={{ scale: 0, rotate: -180 }}
          animate={{ scale: 1, rotate: 0 }}
          exit={{ scale: 0, rotate: 180 }}
          transition={{ type: 'spring', duration: 0.5 }}
          onClick={(e) => e.stopPropagation()}
        >
          <QuestionHeader>
            <QuestionTitle>Code Challenge</QuestionTitle>
            <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
              <DifficultyBadge difficulty={currentQuestion.difficulty}>
                {currentQuestion.difficulty}
              </DifficultyBadge>
              <CloseButton
                onClick={handleClose}
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
              >
                
              </CloseButton>
            </div>
          </QuestionHeader>

          <QuestionText>
            {currentQuestion.question}
          </QuestionText>

          <OptionsContainer>
            {currentQuestion.options.map((option, index) => (
              <OptionButton
                key={index}
                onClick={() => handleAnswerSelect(index)}
                selected={selectedAnswer === index}
                correct={answered && index === currentQuestion.correctAnswer}
                disabled={answered}
                whileHover={!answered ? { scale: 1.02 } : {}}
                whileTap={!answered ? { scale: 0.98 } : {}}
              >
                <OptionLabel>{String.fromCharCode(65 + index)}.</OptionLabel>
                {option}
              </OptionButton>
            ))}
          </OptionsContainer>

          <AnimatePresence>
            {showHint && (
              <HintContainer
                initial={{ opacity: 0, height: 0 }}
                animate={{ opacity: 1, height: 'auto' }}
                exit={{ opacity: 0, height: 0 }}
              >
                <strong> Hint:</strong> {currentQuestion.hint}
              </HintContainer>
            )}
          </AnimatePresence>

          <AnimatePresence>
            {showFeedback && (
              <FeedbackContainer
                correct={isCorrect}
                initial={{ opacity: 0, scale: 0.8 }}
                animate={{ opacity: 1, scale: 1 }}
                exit={{ opacity: 0, scale: 0.8 }}
              >
                <strong>{isCorrect ? ' Correct!' : ' Incorrect'}</strong>
                {currentQuestion.explanation && (
                  <div style={{ marginTop: '10px' }}>
                    {currentQuestion.explanation}
                  </div>
                )}
              </FeedbackContainer>
            )}
          </AnimatePresence>

          <ButtonContainer>
            {!answered ? (
              <>
                <ActionButton
                  variant="hint"
                  onClick={handleHint}
                  disabled={showHint}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                   Get Hint
                </ActionButton>
                <ActionButton
                  variant="primary"
                  onClick={handleSubmit}
                  disabled={selectedAnswer === null}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  Submit Answer
                </ActionButton>
              </>
            ) : (
              <ActionButton
                variant="primary"
                onClick={handleNext}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                {isCorrect ? 'Continue Adventure' : 'Try Again'}
              </ActionButton>
            )}
          </ButtonContainer>
        </ModalContent>
      </ModalOverlay>
    </AnimatePresence>
  );
};

export default QuestionModal;
